<template>
  <div class="location-way js-is-appear">
    <h4 class="location-way__title">  </h4>
    <ol class="location-way-list">
      <li class="location-way-list__item">
         7   ,<br />
         107/ 10-1,10-2,12-3    ,<br />
         3 .
      </li>
      <li class="location-way-list__item">
         7   ,<br />
        72-1,111 ,     3 .
      </li>
      <li class="location-way-list__item">
         7  ,<br />
           10  .
      </li>
    </ol>
  </div>
</template>

<script>
export default {
  name: "location-way",
};
</script>

<style lang="scss" scoped>
.location {
  &-way {
    $w: &;

    &__title {
      margin-bottom: px2rem(15);
      font-size: px2rem(18);
      font-weight: bold;
      text-align: center;
    }
    &-list {
      &__item {
        position: relative;
        margin-bottom: px2rem(24);
        padding-left: px2rem(15);
        font-size: px2rem(14);
        line-height: 1.4em;

        @for $i from 0 through 3 {
          &:nth-child(#{$i}) {
            &::before {
              content: "#{$i}.";
              position: absolute;
              top: 0;
              left: 0;
            }
          }
        }
      }
    }
    &:not(.is-appear) {
      opacity: 0;
      transform: translateY(2rem);

      #{$w}-list__item {
        opacity: 0;
        transform: translateY(2rem);
      }
    }
    &.is-appear {
      opacity: 1;
      transform: translateY(0);

      @include transition;

      #{$w}-list__item {
        @for $i from 0 through 3 {
          &:nth-child(#{$i}) {
            opacity: 1;
            transform: translateY(0);

            @include transition($delay: #{$i * 0.05});
          }
        }
      }
    }
  }
}
</style>
